.main-nav__search-toggle:checked + .main-nav__search-trigger + .main-nav__search-form {
  display: block; }

.main-nav__toggle {
  display: none; }

.main-nav__toggle,
.main-nav__toggle-trigger,
.main-nav__toggle-burger {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  cursor: pointer;
  z-index: 1; }

.main-nav__toggle-burger {
  top: 9px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #FFFFFF;
  border-radius: 10px; }
  .main-nav__toggle-burger::before {
    content: "";
    position: absolute;
    top: -8px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #FFFFFF;
    border-radius: 10px; }
  .main-nav__toggle-burger::after {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #FFFFFF;
    border-radius: 10px; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger {
  width: 0;
  transform: translateX(-14px);
  transition: width 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::before {
  transform: rotate(45deg) translate(0, 13px);
  transition: transform 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::after {
  transform: rotate(-45deg) translate(-2px, -12px);
  transition: transform 0.2s ease; }

