.museums__item-address,
.museums__item-tel {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: #BFBFBF; }

@media (min-width: 768px) {
  .museums__title {
    margin-bottom: 360px; }
  .museums__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  .museums__item {
    width: 48%;
    border: none; }
  .museums__map {
    display: block;
    position: absolute;
    left: 0;
    top: 125px;
    width: 100%; } }

@media (min-width: 1024px) {
  .museums__title {
    margin-bottom: 40px; }
  .museums__map {
    position: static;
    margin-bottom: 40px; }
  .museums__list {
    width: 920px; }
  .museums__item {
    width: 33%; }
  .museums__item:nth-child(2) {
    border-right: 1px solid #e5e5e5;
    border-left: 1px solid #e5e5e5; }
  .museums__item-wrapper {
    padding-top: 0; } }

@media (min-width: 1024px) {
  .museums {
    border: none;
    padding-top: 84px;
    padding-bottom: 105px; }
  .museums__item-wrapper {
    padding-left: 0;
    padding-right: 4px; }
  .museums__item {
    width: 31%; }
  .museums__item:nth-child(1) {
    border-right: 1px solid #e5e5e5; }
  .museums__item:nth-child(2) {
    border-right: 1px solid #e5e5e5;
    border-left: none; } }

.page-footer {
  padding-top: 30px;
  padding-bottom: 58px;
  background-color: #202020; }

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.page-footer__nav .main-nav__list {
  display: block;
  margin-bottom: 25px; }

.page-footer__social {
  width: 132px; }

.page-footer__social .social__icon {
  fill: #F5F5F5; }

.page-footer__social .social__item {
  border: none; }

@media (min-width: 768px) {
  .page-footer__nav {
    width: 100%; }
  .page-footer__nav .main-nav__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 50%;
    margin: 0 auto 25px auto; } }

@media (min-width: 1024px) {
  .page-footer__nav .main-nav__list {
    width: 37%; } }

@media (min-width: 1440px) {
  .page-footer {
    padding-top: 50px;
    padding-bottom: 58px; }
  .page-footer__nav .main-nav__list {
    width: 33%; }
  .page-footer__social {
    width: 153px; } }
